@layer utilities {
	.scrollbar-hide {
		/* IE and Edge */
		-ms-overflow-style: none;

		/* Firefox */
		scrollbar-width: none;
	}

	/* Safari and Chrome */
	.scrollbar-hide::-webkit-scrollbar {
		display: none;
	}

	.scrollbar-default {
		/* IE and Edge */
		-ms-overflow-style: auto;

		/* Firefox */
		scrollbar-width: auto;
	}

	/* Safari and Chrome */
	.scrollbar-default::-webkit-scrollbar {
		display: block;
	}
}
